const inputCurrencyError = ref('');
const dataReady = computed(() => store.state.contract);

const openPaymentMethods = () => {
	store.commit('SHOW_PAYMENT_METHODS', true);
};

const hideComponentHandler = () => {
	setTimeout(() => store.commit('SHOW_PAYMENT_METHODS', false), 300);
};
const hasPaymentInputErrors = () => {
	if (!sum.value) {
		inputCurrencyError.value = 'Введите сумму для частичной оплаты';
	} else if (sum.value > store.state.contract.total_debt) {
		inputCurrencyError.value = 'Сумма платежа не должна превышать сумму долга';
	} else if (sum.value < 1) {
		inputCurrencyError.value = 'Ошибка! Сумма должна быть не меньше одного рубля';
	}

	return !!inputCurrencyError.value;
};

watch(() => store.state.historyFeatures.unfinishedPaySum, (val) => {
	if (val) {
		sum.value = val;
	}
}, { immediate: true });
const pay = async () => {
	if (!hasPaymentInputErrors()) {
		console.log('no errors, redirect to sbp & card data');
	}
	// return new Promise((resolve) => setTimeout(resolve, 2000));
};

watchEffect(() => {
	if (sum.value >= 0) {
		inputCurrencyError.value = '';
	}
});

watch(
	() => store.state.showPaymentMethods,
	(show) => {
		document.body.style.overflow = show ? 'hidden' : 'auto';
	},
);

