export default function useLocalSum(val) {
	// console.log(new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' }).format(val).slice(0, -1));

	if (val || val === 0) {
		const num = new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' }).format(val).slice(0, -1); // число типа 15 154,50
		const [rub, cop] = num.split(',');
		return cop.includes('00') ? rub : num; // отрезаем копейки если число типа 15 000,00 р
	}
	return '';

	// return val || val === 0
	// 	? new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' }).format(val).slice(0, -1)
	// 	: '';
}
