	ref,
} from 'vue';

const props = defineProps({
	handler: {
		type: Function,
		default: () => ({}),
	},
	params: {
		type: Object,
	},
	isPreloader: {
		type: Boolean,
		default: false,
	},
});

const disabled = ref(false);
const preloader = ref(false);

const handlerFunc = async () => {
	if (props.isPreloader) {
		preloader.value = true;
	}
	disabled.value = true;
	await props.handler();
