<template>
	<div class="input-currency">
		<div class="input-currency__input-wrap">
			<input
				@focus="onFocus"
				@blur="onBlur"
				class="input-currency__input"
				ref="inputRef"
			/>
			<div v-if="placeholderVisible" class="input-currency__placeholder">Сумма от 1 р.</div>
		</div>
		<div v-if="inputCurrencyError" class="input-currency__sum-error">{{inputCurrencyError}}</div>
		<div class="input-currency__sum-choice">
			<Button @click="() => updateValue(store.state.contract.percents_sum)" class="button--payment-sum-choice">
				<span>Для продления</span> <span class="number">{{useLocalSum(store.state.contract.percents_sum)}} p.</span>
			</Button>
			<Button @click="() => updateValue(store.state.contract.total_debt)" class="button--payment-sum-choice">
				<span>Для закрытия</span> <span class="number">{{useLocalSum(store.state.contract.total_debt)}} p.</span>
			</Button>
		</div>
	</div>
</template>

<script setup>
import { defineProps, ref, watch } from 'vue';
import { useCurrencyInput } from 'vue-currency-input';
import Button from '@/shared/components/Button';
import { useStore } from 'vuex';
import useLocalSum from '@/shared/lib/hooks/useLocalSum';

const store = useStore();
const props = defineProps({
	modelValue: [String, Number],
	options: Object,
	inputCurrencyError: {
		type: String,
	},
});
const {	inputRef, setValue } = useCurrencyInput(props.options);

const placeholderVisible = ref(true);

const updateValue = (val) => {
	setValue(val);
	placeholderVisible.value = false;
};
const onFocus = () => {	placeholderVisible.value = false; };
const onBlur = () => {
	if (props.modelValue || props.modelValue === 0) {
		placeholderVisible.value = false;
	} else {
		placeholderVisible.value = true;
	}
};

watch(() => store.state.historyFeatures.unfinishedPaySum, (val) => {
	if (val) {
		placeholderVisible.value = false;
	}
}, { immediate: true });
</script>

<style lang="scss" scoped>
@import "input-currency";
</style>
