<template>
	<div class="payment-methods">
		<h2 class="payment-methods__headline">Все способы оплаты</h2>

		<Button
			v-for="(paymentMethod, index) in paymentMethods"
			:key="index" class="button--payment-methods"
			:handler="() => setMethod(paymentMethod)"
			:class="{'active': store.state.paymentMethod === paymentMethod.slug}"
		>
			<div class="button--payment-methods__icon" v-html="paymentMethod.icon"></div>
			<p class="button--payment-methods__text">{{paymentMethod.text}}</p>

			<svg v-if="store.state.paymentMethod === paymentMethod.slug" class="payment-methods__check" xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 48 48" width="30px" height="30px"><path fill="#4caf50" d="M44,24c0,11.045-8.955,20-20,20S4,35.045,4,24S12.955,4,24,4S44,12.955,44,24z"/><path fill="#ccff90" d="M34.602,14.602L21,28.199l-5.602-5.598l-2.797,2.797L21,33.801l16.398-16.402L34.602,14.602z"/></svg>
		</Button>

		<Button class="button--pay-choice" @click="setPayMethod">
			{{buttonText}}
		</Button>
	</div>
</template>

<script setup>
import { computed, defineProps } from 'vue';
import Button from '@/shared/components/Button/Button.vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';

const store = useStore();
const router = useRouter();
const setPayMethod = () => {
	const method = store.state.paymentMethod;

	switch (method) {
	case 'cash':
		router.push({ name: 'addresses' });
		break;
	case 'QR':
		router.push({ name: 'qr-code-payment' });
		break;
	default:
		break;
	}
	store.commit('SHOW_PAYMENT_METHODS', false);
};
const buttonText = computed(() => {
	switch (store.state.paymentMethod) {
	case 'bank':
		return 'Продолжить';

	case
		'cash'
		:
		return 'Перейти на страницу с адресами офисов';
	case
		'QR'
		:
		return 'Перейти к оплате QR-кодом';
	default:
		return 'Продолжить';
	}
});
const setMethod = (method) => {
	// method.handler();
	store.commit('SET_PAYMENT_METHOD', method.slug);
	// store.commit('SHOW_PAYMENT_METHODS', false);
};
defineProps({
	paymentMethods: {
		type: Array,
		default: () => ([]),
	},
});
</script>

<style lang="scss">
@import "payment-methods";
</style>
